import React, {Component} from 'react';
import "./testimo.css"

const TestimonialEn = require('../../assets/translations/en.json');
const TestimonialEs = require('../../assets/translations/es.json');
let testimonial = null;

class Testimonial extends Component{

   render(){

    let { language } = this.props;

    if (language === 'en') {
      testimonial = TestimonialEn
    } else {
      testimonial = TestimonialEs
    }

    return(
            <>
            <div>
              <div class="carousel carousel-main" data-flickity='{ "autoPlay": true, "pageDots": false}'>
                { 
                  testimonial.testimonios.testimoniosItems.map((item, i) => {
                    return (
                      <div className="item" key={i}>
                        <div className="erp_testimonial_item carousel-cell">
                            <div className="content"><p>{item.DescripT}</p></div>
                            <div className="media"><div className="media-body"><h5>{item.NameT}</h5><p>{item.Position}</p></div></div>
                        </div>
                      </div>
                    )
                  })
                                  
                }
              </div>
            </div>
            </>
        )
   }
}

export default Testimonial;

